<template>
  <div class="wrap">
    <div class="index">
        <div class="back_img">
            <img src="@/assets/index/background.png">
        </div>
        <div class="inner">
            <div class="logo" v-on:click="wordsquare()">
                <img src="@/assets/logo.png">
            </div>
            <div class="music_area">
                <p class="txt">Music by : Bensound</p>
                <div class="music_icon">
                    <img class="icon_on" src="@/assets/speaker.png" v-show="false">
                    <img class="icon_off" src="@/assets/speaker_off.png">
                </div>
            </div>
            <div class="title_area">
                <img src="@/assets/index/title.png">
            </div>
            <div class="frame_arae">
                <img src="@/assets/index/frame.png">
            </div>
            <div class="start_area">
                <img src="@/assets/index/start.png" v-on:click="goto_open()">
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
export default {
    name:"indexView",
    methods:{
        goto_open:function(){
            setTimeout(() => {
                this.$router.push('/opening')
            }, 500);
        },
        wordsquare:function(){
            window.open("https://www.wordsquare.org/");
        },
    },
    mounted(){
        var default_on=$('.music_area>.music_icon').find('.icon_on');
        var default_off=$('.music_area>.music_icon').find('.icon_off');
        
        var video = document.getElementById('audio_id'); 
        var is_play=video.paused;
        console.log(is_play);
        if(is_play){
            default_on.hide();
            default_off.hide();
            default_off.css({
                display:'block'
            });
        }else{
            default_on.hide();
            default_off.hide();
            default_on.css({
                display:'block'
            });
        }
        $('.music_area>.music_icon').click(function(){
            var that = $(this);
            console.log(that);
            var on = that.find('.icon_on');
            var off = that.find('.icon_off');
            var on_status = on.css('display');
            var off_status = off.css('display');
            console.log(on_status);
            console.log(off_status);
            if(on_status=='none' && off_status=='block'){
                on.hide();
                off.hide();
                on.css({
                    display:'block'
                });
                document.getElementById('audio_id').play();
            }else if(on_status=='block' && off_status=='none'){
                on.hide();
                off.hide();
                off.css({
                    display:'block'
                });
                document.getElementById('audio_id').pause();
            }
        })
    }
}
</script>

<style lang="scss" scoped>
    @keyframes rotate_ani {
        from{
            transform: rotate(30deg);
        }
        to{
            transform: rotate(-30deg);
        }
    }
    @keyframes blank {
        from{
            opacity: 0.3;
        }
        to{
            opacity: 1;
        }
        
    }
    .index{
        width: 720px;
        height: auto;
        float: left;
        position: relative;
        &>.back_img{
            width: 100%;
            height: auto;
            float: left;
            &>img{
                width: 100%;
                height: auto;
                float: left;
            }
        }
        &>.inner{
            width: 100%;
            height: 100%;
            float: left;
            padding: 30px;
            box-sizing: border-box;
            position:absolute;
            top: 0;
            left: 0;
            &>.logo{
                width: 120px;
                height: auto;
                float: left;
                cursor: pointer;
                &>img{
                    width: 100%;
                    height: auto;
                    float: left;
                }
            }
            &>.music_area{
                width: 260px;
                height: auto;
                float: right;
                &>.txt{
                    width: 100%;
                    height:auto;
                    float: left;
                    font-size: 24px;
                    color: #66c0b4;
                    font-weight: 600;
                }
                &>.music_icon{
                    width: 100%;
                    height: 50px;
                    float: left;
                    position: relative;
                    &>.icon_on,&>.icon_off{
                        width: 50px;
                        height: auto;
                        float: left;
                        position: absolute;
                        right: 0;
                        top: 0;
                    }
                    
                }

            }
            &>.title_area{
                width: 100%;
                height: auto;
                float: left;
                margin-top: 60px;
                &>img{
                    width: auto;
                    height: auto;
                    float: left;
                    position: relative;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
            &>.frame_arae{
                width: 100%;
                height: auto;
                float: left;
                &>img{
                    width: 70px;
                    height: auto;
                    float: right;
                    margin-right: 60px;
                    transform-origin: top;
                    animation-name: rotate_ani;
                    animation-duration: 2s;
                    animation-iteration-count: infinite;
                    animation-direction: alternate-reverse;
                }
            }
            &>.start_area{
                width: 100%;
                height: auto;
                float: left;
                position: absolute;
                bottom: 70px;
                left: 0;
                padding: 0 30px;
                box-sizing: border-box;
                cursor: pointer;
                &>img{
                    width: auto;
                    height: auto;
                    float: right;
                    animation-name: blank;
                    animation-direction: alternate-reverse;
                    animation-duration: 1s;
                    animation-iteration-count: infinite;
                }
            }
        }
        
    }
    @media all and (max-width:720px) and (min-width:320px) {
        .index{
            width: 100%;
            height: auto;
            float: left;
            position: relative;
            &>.back_img{
                width: 100%;
                height: auto;
                float: left;
                &>img{
                    width: 100%;
                    height: auto;
                    float: left;
                }
            }
            &>.inner{
                width: 100%;
                height: 100%;
                float: left;
                padding: 4.16vw;
                box-sizing: border-box;
                position:absolute;
                top: 0;
                left: 0;
                &>.logo{
                    width: 16.66vw;
                    height: auto;
                    float: left;
                    cursor: pointer;
                    &>img{
                        width: 100%;
                        height: auto;
                        float: left;
                    }
                }
                &>.music_area{
                    width: 36.11vw;
                    height: auto;
                    float: right;
                    &>.txt{
                        width: 100%;
                        height:auto;
                        float: left;
                        font-size: 3.33vw;
                        color: #66c0b4;
                        font-weight: 600;
                    }
                    &>.music_icon{
                        width: 100%;
                        height: 6.944vw;
                        float: left;
                        position: relative;
                        &>.icon_on,&>.icon_off{
                            width: 6.944vw;
                            height: auto;
                            float: left;
                            position: absolute;
                            right: 0;
                            top: 0;
                        }
                        
                    }

                }
                &>.title_area{
                    width: 100%;
                    height: auto;
                    float: left;
                    margin-top: 8.33vw;
                    &>img{
                        width: 78.88vw;
                        height: auto;
                        float: left;
                        position: relative;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
                &>.frame_arae{
                    width: 100%;
                    height: auto;
                    float: left;
                    &>img{
                        width: 9.722vw;
                        height: auto;
                        float: right;
                        margin-right: 8.33vw;
                        transform-origin: top;
                        animation-name: rotate_ani;
                        animation-duration: 2s;
                        animation-iteration-count: infinite;
                        animation-direction: alternate-reverse;
                    }
                }
                &>.start_area{
                    width: 100%;
                    height: auto;
                    float: left;
                    position: absolute;
                    bottom: 9.722vw;
                    left: 0;
                    padding: 0 4.16vw;
                    box-sizing: border-box;
                    cursor: pointer;
                    &>img{
                        width: 33.055vw;
                        height: auto;
                        float: right;
                        animation-name: blank;
                        animation-direction: alternate-reverse;
                        animation-duration: 1s;
                        animation-iteration-count: infinite;
                    }
                }
            }
            
        }
    }
</style>