<template>
  <div class="wrap">
    <div class="test">
        <div class="back_img">
            <img src="@/assets/test/background.png">
        </div>
        <div class="inner">
            <div class="video_area">
                <div class="inner_video">
                    <div class="time_area" v-show="timer">
                        <p class="txt">{{rec_time}}</p>
                    </div>
                    <video id="test_video" class="video" controls="true">
                        <source src="@/assets/video/all_test.mp4" type="video/mp4">
                    </video>
                </div>
            </div>
            <div class="btn_area">
                <img src="@/assets/btn/prev.png" class="prev" v-on:click="prev()">
                <img src="@/assets/btn/next.png" class="next" v-on:click="next()">
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    name:"openingView",
    data() {
        return {
            popup:true,
            rec_time:0,
            timer:false
        }
    },
    methods: {
        close_pop:function(){
            this.popup=false;
        },
        prev:function(){
            this.$router.go(-1);
        },
        next:function(){
            this.$router.push('/result_choice');
        },
        timeupdate:function(){
            var vid = document.getElementById("test_video");  
            var time_str=vid.currentTime;
            this.timer=false;
            if(time_str>=21){
                this.timer=true;
                this.rec_time=Math.floor(time_str-20);
            }
        },
        
    },
    mounted(){
        var vid = document.getElementById("test_video");
        var timeupdate = this.timeupdate;
        // var timecaluate = this.timecaluate;
        vid.ontimeupdate = function(){
            document.getElementById('audio_id').pause();
            timeupdate();
        }
    }
}
</script>

<style lang="scss" scoped>
    .test{
        width: 720px;
        height: auto;
        float: left;
        &>.back_img{
            width: 100%;
            height: auto;
            float: left;
            &>img{
                width: 100%;
                height: auto;
                float: left;
            }
        }
        &>.inner{
            width: 100%;
            height: auto;
            float: left;
            padding: 30px;
            box-sizing: border-box;
            position:absolute;
            top: 0;
            left: 0;
            &>.video_area{
                width: 100%;
                height: auto;
                float: left;
                margin-top: 280px;
                position: relative;
                &>.inner_video{
                    width: 525px;
                    height: auto;
                    float: left;
                    position: relative;
                    left: 50%;
                    transform: translateX(-50%);
                    &>.time_area{
                        width: 100%;
                        height: auto;
                        float: left;
                        position: absolute;
                        z-index: 9;
                        top: 20px;
                        &>.txt{
                            width: 100%;
                            height: auto;
                            float: left;
                            font-size: 60px;
                            text-align: center;
                            color: #fff;
                            box-sizing: border-box;
                        }
                    }
                    &>.video{
                        width: 100%;
                        height: auto;
                        float: left;
                    }
                }
            }
            &>.btn_area{
                width: 100%;
                height: auto;
                float: left;
                margin-top: 30px;
                box-sizing: border-box;
                &>img{
                    width: 200px;
                    height: auto;
                    cursor: pointer;
                    &.prev{
                        float: left;
                    }
                    &.next{
                        float: right;
                    }
                }
            }
        }
        
    }
    @media all and (max-width:720px) and (min-width:320px) {
        .test{
            width: 100%;
            height: auto;
            float: left;
            &>.back_img{
                width: 100%;
                height: auto;
                float: left;
                &>img{
                    width: 100%;
                    height: auto;
                    float: left;
                }
            }
            &>.inner{
                width: 100%;
                height: auto;
                float: left;
                padding: 4.16vw;
                box-sizing: border-box;
                position:absolute;
                top: 0;
                left: 0;
                &>.video_area{
                    width: 100%;
                    height: auto;
                    float: left;
                    margin-top: 38.88vw;
                    position: relative;
                    &>.inner_video{
                        width: 72.916vw;
                        height: auto;
                        float: left;
                        position: relative;
                        left: 50%;
                        transform: translateX(-50%);
                        &>.time_area{
                            width: 100%;
                            height: auto;
                            float: left;
                            position: absolute;
                            z-index: 9;
                            top: 2.77vw;
