                        }
                    }
                }
                &>.btn_area{
                    width: 100%;
                    height: auto;
                    float: left;
                    margin-top: 4.16vw;
                    box-sizing: border-box;
                    &>img{
                        width: 27.77vw;
                        height: auto;
                        cursor: pointer;
                        &.prev{
                            float: left;
                        }
                        &.next{
                            float: right;
