<template>
  <div class="wrap">
    <div class="detail">
        <div class="back_img">
            <img src="@/assets/result_choice/background2.png">
        </div>
        <div class="inner">
            <div class="logo" v-on:click="wordsquare()">
                <img src="@/assets/logo.png">
            </div>
            <div class="video_area">
                <div class="inner_video">
                    <video id="test_video" class="video" controls="true">
                        <source src="@/assets/video/leg.mp4" type="video/mp4" v-if="theme==''">
                        <source src="@/assets/video/hip.mp4" type="video/mp4" v-if="theme==''">
                        <source src="@/assets/video/back.mp4" type="video/mp4" v-if="theme==''">
                        <source src="@/assets/video/core.mp4" type="video/mp4" v-if="theme==''">
                    </video>
                </div>
            </div>
            <div class="detail_txt_area">
                <div class="detail_inner_area" v-on:click="detail_choice()">
                    <img src="@/assets/result/bone.png" class="left">
                    <img src="@/assets/result/detail_txt.png" class="txt">
                    <img src="@/assets/result/bone.png" class="right">
                </div>
            </div>
            <div class="btn_area">
                <ul class="btn_list">
                    <li class="item" v-on:click="home()">
                        <img src="@/assets/result/icon1.png" class="icon">
                        <img src="@/assets/result/reset.png" class="btn">
                    </li>
                    <li class="item" v-on:click="wordsquare()">
                        <img src="@/assets/result/icon2.png" class="icon">
                        <img src="@/assets/result/wsbtn.png" class="btn">
                    </li>
                </ul>
                <ul class="btn_list">
                    <li class="item" v-on:click="share_fun()">
                        <img src="@/assets/result/icon3.png" class="icon">
                        <img src="@/assets/result/facebook.png" class="btn">
                    </li>
                    <li class="item" v-on:click="copy()">
                        <img src="@/assets/result/icon4.png" class="icon">
                        <img src="@/assets/result/link_copy.png" class="btn">
                    </li>
                </ul>
            </div>
            <div class="btn_area2">
                <img src="@/assets/btn/prev.png" class="prev" v-on:click="prev()">
                <img src="@/assets/btn/next.png" class="next" v-on:click="next()" v-show="false">
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
    name:"indexView",
    data() {
        return {
            theme:"",
        }
    },
    methods:{
        prev:function(){
            this.$router.go(-1);
        },
        home:function(){
            this.$router.push('/');
        },
        wordsquare:function(){
            window.open("https://www.wordsquare.org/");
        },
        share_fun: async function(){
            try {
                await navigator.share({
                    title: "",
                    url: "",
                });
                console.log(" ");
            } catch (e) {
                var ans=confirm('url ?');
            if(ans){
                var url = '';
                    var textarea = document.createElement("textarea");
                    document.body.appendChild(textarea);
                    url = location.origin+location.pathname;
                    textarea.value = url;
                    textarea.select();
                    document.execCommand("copy");
                    document.body.removeChild(textarea);
                }
            }
        },
        copy:function(){
            var url = '';
            var textarea = document.createElement("textarea");
            document.body.appendChild(textarea);
            url = location.origin+location.pathname;
            textarea.value = url;
            textarea.select();
            document.execCommand("copy");
            document.body.removeChild(textarea);
        },
        detail_choice:function(){
            this.$router.push('/detail_choice');
        }
    },
    mounted(){
        var vid = document.getElementById("test_video");
        var timeupdate = this.timeupdate;
        // var timecaluate = this.timecaluate;
        vid.ontimeupdate = function(){
            document.getElementById('audio_id').pause();
            timeupdate();
        }
        var txt = this.$route.query.txt;
        this.theme=txt;
        var default_on=$('.music_area>.music_icon').find('.icon_on');
        var default_off=$('.music_area>.music_icon').find('.icon_off');
        
        var video = document.getElementById('audio_id'); 
        var is_play=video.paused;
        console.log(is_play);
        if(is_play){
            default_on.hide();
            default_off.hide();
            default_off.css({
                display:'block'
            });
        }else{
            default_on.hide();
            default_off.hide();
            default_on.css({
                display:'block'
            });
        }
        $('.music_area>.music_icon').click(function(){
            var that = $(this);
            console.log(that);
            var on = that.find('.icon_on');
            var off = that.find('.icon_off');
            var on_status = on.css('display');
            var off_status = off.css('display');
            console.log(on_status);
            console.log(off_status);
            if(on_status=='none' && off_status=='block'){
                on.hide();
                off.hide();
                on.css({
                    display:'block'
                });
                document.getElementById('audio_id').play();
            }else if(on_status=='block' && off_status=='none'){
                on.hide();
                off.hide();
                off.css({
                    display:'block'
                });
                document.getElementById('audio_id').pause();
            }
        })
    }
}
</script>

