import Vue from 'vue'
import VueRouter from 'vue-router'
import IndexView from '../views/IndexView'
import OpeningView from '../views/OpeningView'
import TestView from '../views/TestView'
import ResultChoiceView from '../views/ResultChoiceView'
import ResultView from '../views/ResultView'
import Detail_choice from '../views/Detail_choice'
import DetailView from '../views/DetailView'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: IndexView
  },
  {
    path: '/opening',
    name: 'opening',
    component: OpeningView
  },
  {
    path: '/test',
    name: 'test',
    component: TestView
  },
  {
    path: '/result_choice',
    name: 'result_choice',
    component: ResultChoiceView
  },
  {
    path: '/result',
    name: 'result',
    component: ResultView
  },
  {
    path: '/detail_choice',
    name: 'detail_choice',
    component: Detail_choice
  },
  {
    path: '/detail',
    name: 'detail',
    component: DetailView
  }
]
const router = new VueRouter({
  // mode: 'history',
  base: 'muscle_test',
  routes
})

export default router
