<template>
  <div class="wrap">
    <div class="opening">
        <div class="back_img">
            <img src="@/assets/opening/background.png">
        </div>
        <div class="inner">
            <div class="warming_area" v-show="popup">
                <div class="back" v-on:click="close_pop()"></div>
                <div class="img_area">
                    <div class="inner_img">
                        <div class="close_btn" v-on:click="close_pop()"></div>
                        <img src="@/assets/opening/warning.png">
                    </div>
                </div>
            </div>
            <div class="music_area">
                <div class="music_icon">
                    <img class="icon_on" src="@/assets/speaker.png">
                    <img class="icon_off" src="@/assets/speaker_off.png">
                </div>
            </div>
            <ul class="txt_list">
                <li class="item on">
                    <img src="@/assets/opening/text1.png" class="txt1">
                </li>
                <li class="item on">
                    <img src="@/assets/opening/text2.png" class="txt2">
                </li>
                <li class="item">
                    <img src="@/assets/opening/text3.png" class="txt3">
                </li>
                <li class="item">
                    <img src="@/assets/opening/text4.png" class="txt4">
                </li>
                <li class="item">
                    <img src="@/assets/opening/text5.png" class="txt5">
                </li>
                <li class="item">
                    <img src="@/assets/opening/text6.png" class="txt6">
                </li>
            </ul>
            
            <div class="start_area">
                <img src="@/assets/opening/start.png" v-on:click="goto_test()">
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
    name:"openingView",
    data() {
        return {
            popup:true,
        }
    },
    methods:{
        close_pop:function(){
            this.popup=false;
        },
        goto_test:function(){
            this.$router.push('/test');
        }
    },
    mounted(){
        var default_on=$('.music_area>.music_icon').find('.icon_on');
        var default_off=$('.music_area>.music_icon').find('.icon_off');
        
        var video = document.getElementById('audio_id'); 
        var is_play=video.paused;
        if(is_play){
            default_on.hide();
            default_off.hide();
            default_off.css({
                display:'block'
            });
        }else{
            default_on.hide();
            default_off.hide();
            default_on.css({
                display:'block'
            });
        }
        $('.music_area>.music_icon').click(function(){
            var that = $(this);
            var on = that.find('.icon_on');
            var off = that.find('.icon_off');
            var on_status = on.css('display');
            var off_status = off.css('display');
            if(on_status=='none' && off_status=='block'){
                on.hide();
                off.hide();
                on.css({
                    display:'block'
                });
                document.getElementById('audio_id').play();
            }else if(on_status=='block' && off_status=='none'){
                on.hide();
                off.hide();
                off.css({
                    display:'block'
                });
                document.getElementById('audio_id').pause();
            }
        });

        $(window).scroll(function(){
            var body_height = parseInt($('html').css('height'));
            var win_vh = $(window).height();
            var win_vh2 = body_height-win_vh;

            let st = $(window).scrollTop();
            var stPercent = parseInt((st/win_vh2)*100);
            console.log(stPercent);
            if(stPercent>=20){
                $('.txt_list>.item').eq(2).addClass('on');
            }
            if(stPercent>=40){
                $('.txt_list>.item').eq(3).addClass('on');
            }
            if(stPercent>=60){
                $('.txt_list>.item').eq(4).addClass('on');
            }
            if(stPercent>=75){
                $('.txt_list>.item').eq(5).addClass('on');
            }
        })
    }
}
</script>

