<template>
  <div class="wrap">
    <div class="result_choice" v-show="!loading">
        <div class="back_img">
            <img src="@/assets/result_choice/background.png">
        </div>
        <div class="inner">
            <div class="music_area">
                <div class="music_icon">
                    <img class="icon_on" src="@/assets/speaker.png">
                    <img class="icon_off" src="@/assets/speaker_off.png">
                </div>
            </div>
            <div class="list_area">
                <ul class="list">
                    <li class="item" v-on:click="choice(1)">
                        <p class="txt">10 </p>
                    </li>
                    <li class="item" v-on:click="choice(2)">
                        <p class="txt">20 </p>
                    </li>
                    <li class="item" v-on:click="choice(3)">
                        <p class="txt">30 </p>
                    </li>
                    <li class="item" v-on:click="choice(4)">
                        <p class="txt">30 </p>
                    </li>
                </ul>
            </div>
            <div class="btn_area">
                <img src="@/assets/btn/prev.png" class="prev" v-on:click="prev()">
            </div>
        </div>
    </div>
    <div class="loading" v-show="loading">
        <div class="back_img">
            <img src="@/assets/result_choice/background2.png">
        </div>
        <div class="inner">
            <div class="txt_area">
                <div class="img_area">
                    <img src="@/assets/result_choice/txt1.png" class="txt1">
                    <img src="@/assets/result_choice/txt2.png" class="txt2">
                    <img src="@/assets/result_choice/txt3.png" class="txt3">
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
export default {
    name:"resultChoiceView",
    data() {
        return {
            popup:true,
            checkTimer:0,
            loading:false
        }
    },
    methods: {
        choice:function(txt){
            this.loading=true;
            setTimeout(() => {
                this.checkTimer=txt;
                this.$router.push('/result?idx='+this.checkTimer);
            }, 5000);
        },
        close_pop:function(){
            this.popup=false;
        },
        prev:function(){
            this.$router.go(-1);
        },
        next:function(){
            if(this.checkTimer==0){
                alert(' ');
            }
        }
    },
    mounted() {
        var default_on=$('.music_area>.music_icon').find('.icon_on');
        var default_off=$('.music_area>.music_icon').find('.icon_off');
        
        var video = document.getElementById('audio_id'); 
        var is_play=video.paused;
        if(is_play){
            default_on.hide();
            default_off.hide();
            default_off.css({
                display:'block'
            });
        }else{
            default_on.hide();
            default_off.hide();
            default_on.css({
                display:'block'
            });
        }
        $('.music_area>.music_icon').click(function(){
            var that = $(this);
            console.log(that);
            var on = that.find('.icon_on');
            var off = that.find('.icon_off');
            var on_status = on.css('display');
            var off_status = off.css('display');
            if(on_status=='none' && off_status=='block'){
                on.hide();
                off.hide();
                on.css({
                    display:'block'
                });
                document.getElementById('audio_id').play();
            }else if(on_status=='block' && off_status=='none'){
                on.hide();
                off.hide();
                off.css({
                    display:'block'
                });
                document.getElementById('audio_id').pause();
            }
        })
    },
}
</script>

